*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  border: none;
  outline: none;
  scroll-behavior: smooth;
  font-family:Verdana, Geneva, Tahoma, sans-serif;}
:root{
  --bg-color: #1f242d;
  --second-bg-color: #323946;
  --text-color: #fff;
  --main-color: #0ef;}
html {
  font-size: 62.5%;
  overflow-x: hidden;
}
body{
  background-color: var(--bg-color);
  color: var(--text-color);
}
span {
    color: var(--main-color);
}
.span{
  color: rgba(247, 90, 0, 0.89);
}
section{
  min-height:100vh;
  color: var(--text-color);
}












































@media (max-width: 1200px) {
    html {
        font-size: 55%;
    }
}

@media (max-width: 991px) {
    .header {
        padding: 2rem 3%;
    }

    section {
        padding: 10rem 3% 2rem;
    }

    .contact {
        min-height: auto;
    }
    .footer {
        padding: 2rem 3%;
    }
}
@media (max-width: 780px) {
    #menu-icon{
        display: block;
    }

    .navbar {
        position: absolute;
        top: 100%;
        left:0;
        width: 100%;
        padding: 1rem 3%;
        background: var(--bg-color);
        border-top: .1rem solid rgba(0, 0, 0, .2);
        box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .2);
        display: none;
    }
    .navbar.active {
        display: block;
    }
    .navbar a{
        display: block;
        font-size: 2rem;
        margin: 3rem 0;
        
    }
    .home {
        flex-direction: column;
    }
    .home-content h1{
        font-size: 2.6rem;
    }
    .home-content h3 {
        font-size: 5rem;
    }
    .home-img img{
        width: 70vw;
        margin-top: 4rem;
    }
    .apropos {
        flex-direction: column-reverse;
    }
    .apropos-img img{
        width: 70vw;
        margin-top: 4rem;
    }
  
}



@media (max-width: 450px) {
    html {
        font-size: 50%;

    }
    .contact form .input-box input {
        width: 100%;
    }
}
@media (max-width: 365px) {
    .home-img img {
        width: 90vw;
    }
    .about-img img {
        width: 90vw;
    }
    .footer {
        flex-direction: column-reverse;
    }
    .footer p{
        text-align: center;
        margin-top: 2rem;
    }
}

